.d_container{
    width: 90%;
    margin: auto;
}

.d_container div{
    text-align: center;
}

.d_container h2{
    font-size: 5vw;
    padding-bottom: 4vw;
}

.img_Carroussel{
    width: 30vw;
    text-align: center;
    border-radius: 1vw;
    box-shadow: 0px 0px 5px #e504ec;
    margin-bottom: 3vw;
    margin-top: 1vw;
    margin-left: 1vw;
    margin-right: 1vw;

}

@media (max-width: 800px) {

    .d_container{
        width: 100%;
    }

    .img_Carroussel{
        width: 40vw;
    }
    
}